import React, { Component } from 'react'
import { Container, Row, Col } from 'reactstrap'
import DataTable from './Tables/UserDataTable'
import UserService from "../services/user.service"
import { connect } from 'react-redux'
import ToastAlert from './Toast/Toast'
import HouseCard from './Card/HouseCard'
import ModalForm from './Modals/AddUserModal'
import { setMenu } from '../store/actions'
import axios from "axios"
import { config } from '../config.js'
import { FaSearch, FaPencilAlt } from 'react-icons/fa'
import { MdSend } from 'react-icons/md'
import Input from "react-validation/build/input"
import Form from "react-validation/build/form"

class MessageList extends Component {
    constructor() {
        super();
        this.state = {
            users: [],
            messages: [],
            selectedUser: 0,
            items: [],
            searchValue: '',
            toastShow: false
        };
        this.searchOnChange = this.searchOnChange.bind(this);
        this.selectUser = this.selectUser.bind(this);
    }

    searchOnChange = (event) => {
        this.setState({ searchValue: event.target.value });
    }

    selectUser(userid) {
        this.setState({ selectedUser: userid });
        this.getMessages(userid);
    }

    componentDidMount() {
        this.props.dispatch(setMenu(3));
        this.getUsers();
        this.getMessages();
    }

    getUsers() {
        UserService.getUsersByMessage().then(
            response => {
                if (response.data.status == "success") {
                    this.setState({ users: response.data.data });
                }
            },
            error => {
                this.setState({
                    content:
                        (error.response && error.response.data) ||
                        error.message ||
                        error.toString()
                });
            }
        );
    }

    getMessages(userid) {
        UserService.getMessages(userid).then(
            response => {
                if (response.data.status == "success") {
                    this.setState({ messages: response.data.data });
                }
            },
            error => {
                this.setState({
                    content:
                        (error.response && error.response.data) ||
                        error.message ||
                        error.toString()
                });
            }
        );
    }


    render() {
        const filterUsers = this.state.users.filter(user => {
            return user.username.toLowerCase().includes(this.state.searchValue.toLowerCase())
        })

        return (
            <div className="component-container">
                <div className="container">
                    <Container className="App">
                        <Row>
                            <Col>
                                <Form className="component-header-form">
                                    <h1 className="component-title">Messages</h1>
                                    <div className="search">
                                        <FaSearch />
                                        <Input
                                            type="text"
                                            className="form-control"
                                            placeholder="Search"
                                            value={this.state.searchValue}
                                            onChange={this.searchOnChange}
                                        />
                                    </div>
                                </Form>
                            </Col>
                        </Row>
                        <Row className="component-toolbar">
                            <Col>
                                <div className="message-container">
                                    <div className="message-name-container">
                                        {filterUsers.map(user => (
                                            <div key={user.id} className={"message-name-card" + (this.state.selectedUser == user.id ? " active" : "")} onClick={() => this.selectUser(user.id)}>
                                                <div className="blue-circle"></div>
                                                <p className="name">{user.username}</p>
                                            </div>
                                        ))}
                                    </div>
                                    <div className="message-content-container">
                                        <div className="message-content-box overflow-scroll">
                                            {this.state.messages.map(message => (
                                                <div key={message.id} className={"message-card-container" + (this.state.selectedUser == message.sender ? " receive" : " send")}>
                                                    <div className={"message-content-card" + (this.state.selectedUser == message.sender ? " receive" : " send")}>
                                                        <p>{message.content}</p>
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                        <div className="message-input-box">
                                            <Form>
                                                <FaPencilAlt />
                                                <Input
                                                    type="text"
                                                    className="form-control"
                                                    placeholder="Type a message"
                                                />
                                            </Form>
                                            <div className="send-box">
                                                <MdSend />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                    <ToastAlert show={this.state.toastShow} description={this.state.alertDescription}></ToastAlert>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => state.admin;

export default connect(mapStateToProps)(MessageList);